



















import {mapState,mapMutations} from 'vuex'

export default {
	data() {
		return {
			
		};
	},
	onLoad(res){
		console.log(res)
	},
	methods:{
		...mapMutations(['getUserMessage']),
		getUserInfo: function(res){
			let userInfo = res.detail.userInfo
			if(userInfo){
				this.getUserMessage(userInfo)
				this.switchIndex()
			}
		},
		switchIndex: () => {
			wx.switchTab({
				url: '/pages/tabbar/tabbar-5/tabbar-5'
			})
		}
	}
}
